// AppContent.js
import React from 'react';
import { Routes, Route, useLocation } from 'react-router-dom';
import Header from './component/Header';
import Main from './component/Main';
import DisplayPage from './component/DisplayPage';
import SosReportPage from './component/SosReportPage';
import { useKakaoLoader } from 'react-kakao-maps-sdk';
import styled from 'styled-components';

export default function AppContent() {
  const location = useLocation();
  const [loading, error] = useKakaoLoader({
    appkey: 'cfd7264f6f0677bc208104d9cbc45701',
    libraries: ['services'],
  });

  const hideHeader = location.pathname === '/sosreport';

  if (loading) return <div>  ...</div>;
  if (error) return <div>: {String(error.message || error)}</div>;

  return (
    <div className="App">
      <Container>
        <Wrapper>
          {!hideHeader && <Header />}
          <Routes>
            <Route path="/" element={<Main />} />
            <Route path="/displaypage" element={<DisplayPage />} />
            <Route path="/sosreport" element={<SosReportPage />} />
          </Routes>
        </Wrapper>
      </Container>
    </div>
  );
}


const Container = styled.div`
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
`

const Wrapper = styled.div`
  width: 400px;
  height: 300px;
  background-color: lightblue;
`