import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios'; // axios import
import './SosModal.css';
import { useNavigate } from 'react-router-dom';
import { routeCoords } from '../component/Main'; // Main.js routeCoords import


const SosModal = ({ onSendData, onClose }) => {
    const modalBackground = useRef();
    const navigate = useNavigate();

    // Close modal on ESC key press
    useEffect(() => {
        const handleKeyDown = (e) => {
            if (e.key === 'Escape') {
                onClose();
            }
        };

        document.addEventListener('keydown', handleKeyDown);
        return () => {
            document.removeEventListener('keydown', handleKeyDown);
        };
    }, []);

    const handleBackgroundClick = (e) => {
        if (e.target === modalBackground.current) {
            onClose();
        }
    };

    // "  "     
    const handleSendData = async (situation) => {
        const requestData = {
            lat: 36.361349,
            lng: 127.344596,
            name: "",
            rrn: "030331-1234567",
            phone: "010-4669-2902",
            situation: situation
        };

        try {
            const postResponse = await axios.post('https://backend-1-ria5.onrender.com/api/sos', requestData);
            console.log('POST Response:', postResponse.data); // POST     

            // GET   
            const getResponse = await axios.get('https://backend-1-ria5.onrender.com/api/sos');
            console.log('GET Response:', getResponse.data); // GET     

            //    
            const latestData = getResponse.data[getResponse.data.length - 1];


            // Main   
            navigate('/', { state: { route: latestData } });

            onClose();
        } catch (error) {
            console.error('Error:', error); //    
            alert('      .');
        }
    };

    return (
        <>
            {(
                <div
                    className="modal-overlay"
                    ref={modalBackground}
                    onClick={handleBackgroundClick}
                >
                    <div className="modal-content">
                        <div className="modal">
                            <span role="img" aria-label="siren"></span>
                            SOS
                            <span role="img" aria-label="siren"></span>
                        </div>
                        <button
                            className="modal-close-btn"
                            onClick={() => onClose()}
                        >
                            X
                        </button>
                        <div className="button-container">
                            <button className="modal-button" onClick={() => handleSendData("")}>
                                <img
                                    className="icon"
                                    src={`${process.env.PUBLIC_URL}/data/Stalking.png`}
                                    alt="Stalking icon"
                                />
                                
                            </button>
                            <button
                                className="modal-button" onClick={() => handleSendData("")}>
                                <img
                                    className="icon"
                                    src={`${process.env.PUBLIC_URL}/data/Harassment.png`}
                                    alt="Harassment icon"
                                />
                                
                            </button>
                            <button className="modal-button" onClick={() => handleSendData("    ")}>
                                <img
                                    className="icon"
                                    src={`${process.env.PUBLIC_URL}/data/Drunk.png`}
                                    alt="Knife icon"
                                />
                                  
                            </button>
                            <button className="modal-button" onClick={() => handleSendData(" ")}>
                                <img
                                    className="icon"
                                    src={`${process.env.PUBLIC_URL}/data/Drunk.png`}
                                    alt="Drunk icon"
                                />
                                 
                            </button>
                        </div>
                        <div className="modal-text">
                                    .
                        </div>
                    </div>
                </div>
            )}
        </>
    );
};

export default SosModal;